
.contact {
  background-color: #0a0a0a;
  color: #fff;
  padding: 80px 20px;
  text-align: center;
}

.contact h2 {
  font-size: 2.5rem;
  color: #00c6ff;
  margin-bottom: 20px;
}

.contact p {
  color: #ccc;
  margin-bottom: 30px;
}

form {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin: auto;
}

input, textarea {
  margin: 10px 0;
  padding: 12px 15px;
  border-radius: 10px;
  border: none;
  outline: none;
  background: #1a1a1a;
  color: #fff;
  font-family: sans-serif;
}

input:focus, textarea:focus {
  box-shadow: 0 0 8px #00c6ff;
}

.btn {
  background: #00c6ff;
  color: #fff;
  border: none;
  padding: 12px 25px;
  border-radius: 25px;
  margin-top: 10px;
  cursor: pointer;
  transition: 0.3s;
  font-family: sans-serif;
  font-size: 16px; ;
}

.btn:hover {
  background: #007acc;
    transform: translateY(-12px);
  box-shadow: 0 0 25px rgba(0, 198, 255, 0.45);
  border-color: #00c6ff;
}