.footer {
  background-color: #0a0a0a;
  padding: 40px 20px;
  text-align: center;
  color: #ccc;
}

.social-icons {
  margin-bottom: 20px;
}

.social-icons a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin: 0 12px;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  background: #1a1a1a;
  color: #00c6ff;
  font-size: 20px;
  transition: 0.3s ease;
}

.social-icons a:hover {
  background: #00c6ff;
  color: #fff;
  transform: translateY(-6px);
  box-shadow: 0 0 15px #00c6ff;
}

.footer p {
  font-size: 14px;
  margin-top: 10px;
}
