* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.home {
  width: 100vw;
  height: 100vh;
  background: #0b0f14;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.home-content {
  text-align: center;
  max-width: 700px;
  padding: 20px;
}

.home-content h3 {
  font-size: 22px;
  color: #00c6ff;
  margin-bottom: 10px;
}

.home-content h1 {
  font-size: 52px;
  font-weight: 700;
  margin-bottom: 10px;
}

.home-content h2 {
  font-size: 30px;
  margin-bottom: 20px;
}

.home-content h2 span {
  color: #00c6ff;
}

.home-content p {
  font-size: 16px;
  line-height: 1.7;
  color: #cfd8dc;
  margin-bottom: 30px;
}

.home-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.btn {
  padding: 12px 28px;
  border-radius: 30px;
  text-decoration: none;
  font-weight: 500;
  transition: 0.3s ease;
  font-family: sans-serif;
}

.btn.primary {
  background: #00c6ff;
  color: #000;
}

.btn.primary:hover {
  box-shadow: 0 0 20px #00c6ff;
}

