.projects {
  width: 100%;
  padding: 100px 20px;
  background: #0b0f14; 
  color: #fff;
  text-align: center;
}

.projects h2 {
  font-size: 42px;
  margin-bottom: 60px;
  color: #00c6ff;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 30px;
  max-width: 1100px;
  margin: auto;
}

.project-card {
  background: rgba(255, 255, 255, 0.04);
  border: 1px solid rgba(0, 198, 255, 0.15);
  padding: 35px 25px;
  border-radius: 18px;
  backdrop-filter: blur(10px);
  transition: all 0.4s ease;

  /* Scroll animation start */
  opacity: 0;
  transform: translateY(60px);
}

.project-card.show {
  opacity: 1;
  transform: translateY(0);
}

.project-card h3 {
  font-size: 22px;
  margin-bottom: 12px;
  color: #ffffff;
}

.project-card p {
  font-size: 15px;
  line-height: 1.6;
  color: #cfd8dc;
  margin-bottom: 20px;
}

.project-card:hover {
  transform: translateY(-12px);
  box-shadow: 0 0 25px rgba(0, 198, 255, 0.45);
  border-color: #00c6ff;
}
.project-card i {
  font-size: 40px;
  color: #00c6ff;
  margin-bottom: 18px;
}

