* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

/* NAVBAR */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 65px;
  background: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
  transition: 0.4s ease;
  z-index: 1000;
}

/* SCROLL EFFECT */
.navbar.scrolled {
  background: #0f172a;
  box-shadow: 0 6px 25px rgba(0, 198, 255, 0.25);
}

/* LOGO */
.logo {
  color: #00c6ff;
  font-size: 22px;
  font-weight: 600;
}

/* MENU */
.menu {
  display: flex;
  gap: 25px;
}

.menu a {
  color: white;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 6px;
  position: relative;
  padding: 6px 0;
  transition: 0.3s;
}

/* ICON */
.menu a i {
  color: #00c6ff;
}

/* UNDERLINE ANIMATION */
.menu a::after {
  content: "";
  position: absolute;
  width: 0;
  height: 2px;
  background: #00c6ff;
  left: 0;
  bottom: 0;
  transition: 0.3s;
}

.menu a:hover::after,
.menu a.active::after {
  width: 100%;
}

/* ACTIVE */
.menu a.active {
  color: #00c6ff;
}

/* HAMBURGER */
.hamburger {
  display: none;
  font-size: 24px;
  color: #00c6ff;
  cursor: pointer;
}

/* MOBILE */
@media (max-width: 768px) {
  .menu {
    position: absolute;
    top: 65px;
    right: 0;
    background: #0f172a;
    flex-direction: column;
    width: 220px;
    padding: 20px;
    transform: translateX(100%);
    transition: 0.4s ease;
  }

  .menu.open {
    transform: translateX(0);
  }

  .hamburger {
    display: block;
  }
}
