/* Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* About Section */
.about {
  min-height: 100vh;
  width: 100%;
  background: #0b0f14;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  padding: 60px 20px;
}

/* Card Container */
.about-content {
  max-width: 900px;
  background: rgba(255, 255, 255, 0.04);
  padding: 50px;
  border-radius: 20px;
  text-align: center;
  backdrop-filter: blur(10px);
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

/* 3D Hover Effect */
.about-content:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 25px 60px rgba(0, 198, 255, 0.2);
}

/* Title */
.about-content h2 {
  font-size: 38px;
  margin-bottom: 25px;
  color: #00c6ff;
}

/* Text Wrapper */
.about-text p {
  font-size: 16px;
  line-height: 1.8;
  color: #cfd8dc;
  margin-bottom: 35px;
}

/* Button */
.about-text button {
  padding: 14px 32px;
  border-radius: 30px;
  border: none;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  background: #00c6ff;
  color: #000;
  transition: all 0.3s ease;
}

/* Button Hover */
.about-text button:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 0 25px #00c6ff;
}

/* Fade-in Animation */
.about-content {
  animation: fadeUp 1.2s ease forwards;
}

@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(40px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .about-content {
    padding: 30px;
  }

  .about-content h2 {
    font-size: 30px;
  }

  .about-text p {
    font-size: 15px;
  }

  /* Disable heavy hover on mobile */
  .about-content:hover {
    transform: none;
    box-shadow: none;
  }
}


/* Typing Animation */
.typing-title {
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 3px solid #00c6ff;
  animation: typing 2.5s steps(20, end), blink 0.7s infinite;
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}

@keyframes blink {
  50% { border-color: transparent }
}
