/* Theme Variables */
:root {
  --bg: #020617;
  --card-bg: rgba(255, 255, 255, 0.08);
  --text: #ffffff;
  --accent: #22d3ee;
}

.light {
  --bg: #f8fafc;
  --card-bg: rgba(255, 255, 255, 0.55);
  --text: #020617;
  --accent: #6366f1;
}

/* Section */
.services {
  padding: 90px 20px;
  background: var(--bg);
  color: var(--text);
  transition: 0.3s;
}

.services h2 {
  text-align: center;
  font-size: 2.3rem;
  margin-bottom: 50px;
  color: var(--accent);
}

/* Grid */
.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 30px;
  max-width: 1100px;
  margin: auto;
}

/* Glass Card */
.service-box {
  background: var(--card-bg);
  backdrop-filter: blur(14px);
  border-radius: 20px;
  padding: 35px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.2);

  opacity: 0;
  transform: translateY(60px);
  transition: all 0.6s ease;
}

.service-box.show {
  opacity: 1;
  transform: translateY(0);
}

.service-box i {
  font-size: 2.5rem;
  color: var(--accent);
  margin-bottom: 15px;
}

.service-box:hover {
  transform: translateY(-12px) scale(1.05);
  box-shadow: 0 0 40px rgba(34, 211, 238, 0.4);
}
