
 .Skills {
  background-color: #111;
  color: #fff;
  padding: 80px 20px;
  text-align: center;
}

.Skills h2 {
  font-size: 2.5rem;
  color: #00c6ff;
  margin-bottom: 40px;
}

.Skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  max-width: 1000px;
  margin: auto;
}

.card {
  background: #1c1c1c;
  padding: 25px;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0, 198, 255, 0.3);
  transition: 0.3s;
}

.card:hover {
  transform: translateY(-8px);
  box-shadow: 0 0 20px rgba(0, 198, 255, 0.6);
}

.card h3 {
  color: #00c6ff;
  margin-bottom: 10px;
}

.card p {
  color: #ccc;
  margin-bottom: 15px;
}

.btn {
  background: #00c6ff;
  color: #fff;
  padding: 10px 20px;
  border-radius: 25px;
  text-decoration: none;
  transition: 0.3s;
}

.btn:hover {
  background: #007acc;
} 